<template>
	<div class="walletManage">
		<!--列表-->
	    <mu-list style="padding: 20px;">
	        <mu-list-item v-for="item,index in walletList" :key="index" @click.stop="changeWallet(item)" avatar button :style="item.isUse?'background:linear-gradient(to right, #9586FD , #A0A1FD);':'background: #e5e5e5;'" style="padding: 10px;margin-bottom: 10px;border-radius: 8px;">
		    <mu-list-item-action>
	          <img :src="item.avatar || defaultAvatar" alt="" width="50" height="50" style="border-radius: 50%;">
	        </mu-list-item-action>
	        <mu-list-item-content style="margin-left: 5px;">
		        <mu-list-item-title>{{item.walletName}}</mu-list-item-title>
		        <mu-list-item-sub-title>{{item.address}} </mu-list-item-sub-title>
	        </mu-list-item-content>
	        <mu-list-item-action>
	        	<mu-button icon @click.stop="goToMyWallet(item)">
		          <span class="icon iconfont" style="color: white;font-size: 24px;">&#xe695;</span>
		        </mu-button>
	        </mu-list-item-action>
	      </mu-list-item>
	    </mu-list>
	    
	    <div style="padding:20px 40px;">
	    <router-link to="/addWallet">
	     <mu-button style="height: 44px;width: 100%;" color="#4876F6">添加钱包</mu-button>
	    </router-link>
	    <!--<router-link v-if="walletList.length==0" to="/wallet/creatWallet">
	     <mu-button full-width color="#B9862F" textColor="#fff">创建钱包</mu-button>
	    </router-link>-->
	    </div>
	    
	     <mu-dialog :title="'是否切换钱包?'" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="chooseWallet">
		    <mu-button slot="actions" flat color="primary" @click="closeChoose">否</mu-button>
		    <mu-button slot="actions" flat color="primary" @click="confirmChoose">确定</mu-button>
		  </mu-dialog>
	</div>
</template>

<script>
export default {
  name: 'WalletManage',
  data(){
  	return{
  		walletList:[],
  		chooseWalletId:'',
  		chooseWalletAddress:'',
  		chooseWallet:false,
  		defaultAvatar:require('../../assets/img/logo.png')
  	}
  },
  mounted(){
  	this.walletList = JSON.parse(localStorage.getItem('walletArr'));
  	console.log(this.walletList);
//	this.getWalletList();
  },
  methods:{
  	//选择钱包
  	changeWallet(item){
  		this.chooseWalletAddress = item.address;
  		this.chooseWallet=true;
  	},
  	closeChoose(){
  		this.chooseWallet=false;
  	},
  	confirmChoose(){
  		for(let i in this.walletList){
	  		if(this.walletList[i].address == this.chooseWalletAddress){
	  			this.$set(this.walletList[i],'isUse',true)
	  			localStorage.setItem('chooseWallet',JSON.stringify(this.walletList[i]));
	  			this.chooseWallet=false;
	  		}else{
	  			this.$set(this.walletList[i],'isUse',false)
	  		}
  		}
	  	localStorage.setItem('walletArr',JSON.stringify(this.walletList));
  	},
  	//钱包详情
  	goToMyWallet:function(item){
  		this.$router.push({path:'/myWallet',query:{details:JSON.stringify(item)}})
  	},
//	getWalletList(){
//		let _this = this;
//		_this.$http.get(_this.$lib.url + 'wallet/lists').then(res => {
//					console.log(res);
//			if (res.status == 0) {
//				_this.walletList = res.data;
//				if(res.data){
//					localStorage.setItem('walletId',res.data[0].id)
//				}
//			}
//		}).catch(err => {})
//	}
  },
  computed:{
  	
  },
 }
</script>

<style scoped>
.walletManage {
	
}
</style>


